import React, { Component } from 'react';
import logo from './images/retard.jpg';
import './App.scss';
import 'bootstrap/dist/css/bootstrap.min.css';
import Buttonselect from "./Buttonselect";
import svino from './audio/svino.mp3';
import denapanto from "./audio/denapanto.mp3";
import denapasxolei from "./audio/denapasxolei.mp3";
import denepithimousa from "./audio/denepithimousa.mp3";
import kill from "./audio/kill.mp3";
import ax from "./audio/ax.mp3";
import stopandstare from "./audio/stopandstare.mp3";
import denkatalabaino from "./audio/denkatalabaino.mp3";
import denmafora from "./audio/denmafora.mp3";
import dinomathima from "./audio/dinomathima.mp3";
import exetastiki from "./audio/exetastiki.mp3";
import minenoxliseis from "./audio/minenoxliseis.mp3";
import vathiaepithimia from "./audio/vathiaepithimia.mp3";
import axmanoula from "./audio/axmanoula.mp3";


class App extends Component {

  state = {
    reveal: 1,
    showbutton: false

  }


  counter = 0;
  arr1= [];
  arr2= [];


  loud = (evt) => {
    if(this.arr1.indexOf(evt.target.name) === -1) {
    this.arr1.push(evt.target.name);
    if(this.arr1.length > 3){
      this.setState({
        reveal: 2
      })
    }
  }




    if(evt.target.name === "postavlepeis") {
      new Audio(denapanto).play();
    }

    else if(evt.target.name === "oikonomiki"){
      new Audio(denepithimousa).play();
    }
    else {
      new Audio(denapasxolei).play();
    }
  }

  loud2 = (evt) => {
    if(this.arr2.indexOf(evt.target.name) === -1) {
    this.arr2.push(evt.target.name);
    if(this.arr2.length > 5){
      this.setState({
        reveal: 4
      })
    }
  }

  if(evt.target.name === "provlimatismenos"){
    new Audio(denkatalabaino).play();

  }
  else if(evt.target.name === "apergia"){
    new Audio(denmafora).play();
  }
  else if(evt.target.name === "syneleysi"){
    new Audio(dinomathima).play();
  }
  else if(evt.target.name === "katalipsi"){
    new Audio(exetastiki).play();
  }
  else if(evt.target.name === "eythini") {
    new Audio(minenoxliseis).play();
  }
  else {
    new Audio(vathiaepithimia).play();
  }
  }

  loud3 = (evt) => {
    if(evt.target.name === "kill") {
       new Audio(kill).play();
    }
    else if(evt.target.name === "klotsa") {
      this.counter++;
      if(this.counter === 3){
        new Audio(axmanoula).play();
        this.counter=0;
      }
      else {
       new Audio(ax).play();
     }
    }
    else {
      new Audio(stopandstare).play();
      this.setState({
        reveal: 5
      })
    }
  }

  questions2 = () => {
    this.setState({
      reveal: 3
    })
  }

  render() {



    return (
      <div className="App">
        <header className="App-header">
          <h1 className="text-center font-weight-bold mb-5 bg-info">ΡΩΤΑ ΤΟΝ ΧΡΗΣΤΟ</h1>


        </header>
         <Buttonselect reveal={this.state.reveal}
           questions2={this.questions2}
            loud={this.loud} loud2={this.loud2}
            loud3={this.loud3}
          />





      </div>
    );
  }
}

export default App;
